.publications {
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #F5F5FA;
  @include media-mobile {
    width: 95%;
  }
}

.publications__top-link {
  margin-bottom: 15px;
}


.publications__subheading {
  font-size: 18px;
  margin-bottom: 10px;
  color: #24A3FF;
  font-weight: 500;
  line-height: 1.2;
}

.publications__list {
  display: flex;
  gap: 40px;
  flex-wrap: wrap;
}

.publications__item {
  width: 400px;
  background: #FFFFFF;
  border-radius: 8px;
  padding: 20px;
  @include media-tablet {
    width: 46%;
  }
  @include media-mobile {
    width: 100%;
  }
}


.publications__image {
  width: 100%;
  margin-bottom: 20px;
}

.publications__date {
  padding-bottom: 20px;
  border-bottom: 4px solid #E6E6F0;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.2;
  color: #B3B3BA;
}

.publications__text {
  padding-top: 20px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
}
